}

body {
  font-family: 'Roboto', sans-serif;
  }

label {
  font-size: .7rem;
}

input.form-control, input.form-control:focus {
  background: #F0F0FF;
}

Form {
  max-width: 480px;
  max-height: 100%;
  display: flex;
  flex-direction: column;
}
#submit {
  height: 56px;
}
#check {
  height: 21px;
  width: 21px;
}
.userInfo {
  height: 54px;
  width: 480px;
}
#comments {
  height: 357px;
  width: 480px;
}
.error {
  color: red;
  font-size: 15px;
}
#confirmation {
  display: flex;
}
.wrapper {
  max-width: 555px;
  max-height:652px ;
}




.modal-suc {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}
.modal-suc.act {
    opacity: 1;
    pointer-events: all;

}
.content {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
}
